<template>
  <div class="goods-detail">
    <SellHeader :seller="seller"></SellHeader>
    <nav class="nav">
      <router-link to="./home" class="nav-items" active-class="nav-active">商品</router-link>
      <router-link to="./house" class="nav-items" active-class="nav-active">评价</router-link>
      <router-link to="./house" class="nav-items" active-class="nav-active">商家</router-link>
    </nav>
    <div class="foods">
      <div class="menu-wrapper" ref="menuWrapper">
        <ul>
          <li class="menu-items" v-for=" (item,index) in goods" :class="{'current':currentIndex ===index}" @click="selectedMenu(index,$event)">
            <span class="text"><span v-show="item.type > 0" class="icon" :class="classMap[item.type]" v-cloak></span>{{item.name}}</span>
          </li>
        </ul>
      </div>
      <div class="foods-wrapper" ref="foodsWrapper">
        <ul>
          <li v-for="item in goods" class="food -list food-list-hook">
            <header class="title">{{item.name}}</header>
            <ul>
              <li v-for="food in item.foods" class="food-item">
                <img class="avatar" :src="food.icon" alt="" width="57" height="57">
                <div class="content">
                  <p class="food-name">{{food.name}}</p>
                  <p class="description">{{food.description}}</p>
                  <div class="extra">
                    <span class="sell-count">月售{{food.sellCount}}</span><span class="sell-rate">好评率{{food.rating}}%</span>
                  </div>
                  <div class="price">
                    <span class="now-price">¥ {{food.price}}</span><span class="old-price" v-show="food.oldPrice">¥{{food.oldPrice}}</span>
                  </div>
                  <div class="control-wrapper">
                    <control :food="food"></control>
                  </div>
                </div>
              </li>
            </ul>
          </li>
        </ul>
      </div>
    </div>
    <goodFooter ref="goodfooter" :min-price="seller.minPrice" :delivery-price="seller.deliveryPrice" :select-goods="selectGoods"></goodFooter>
  </div>
</template>

<script>
  import SellHeader from '../header/SellHeader'
  import control from '../../../components/goods/control'
  import goodFooter from '../../../components/goods/goodsFooter'
  import {sendSellerObj} from '../../../config/variable'
  import BScroll from 'better-scroll'
  //  import Bus from '../../../services/event/eventBus'

  export default {
    name: 'GoodsDetail',
    data() {
      return {
        seller: {},
        classMap: ['decrease', 'discount', 'guarantee', 'invoice', 'special'],
        goods: [],
        scrollY: 0,
        listHeight: [],
        selectedGood: {}
      }
    },
    computed: {
      //计算当前的索引值
      currentIndex() {
        let len = this.listHeight.length;
        for (let i = 0; i < len; i++) {
          let minHeight = this.listHeight[i];
          let maxHeight = this.listHeight[i + 1];
          if (!maxHeight || (minHeight <= this.scrollY && this.scrollY < maxHeight)) {
            return i;
          }
        }
        return 0;
      },
      selectGoods() {
        let goods = [];
        this.goods.forEach((good) => {
          good.foods.forEach((food) => {
            if (food.count) {
              goods.push(food);
            }
          });

        });
        console.log(goods);
        return goods;
      }
    },
    mounted() {
      //初始化底部导航栏
      this.$store.dispatch("FOOTER_HIDE");
      //发起网络请求,查询页面信息
      this.queryMenu();

//      Bus.$on('add.count', el => {
//        this.$nextTick(() => {
//          console.log(this.$refs.goodfooter.drop);
//          this.$refs.goodfooter.drop(el);
//        })
//      });
    },
    methods: {
      async queryMenu() {
        let _this = this;
        await this.$http.get(sendSellerObj.url).then((res) => {
          _this.seller = res.seller;
          _this.goods = res.goods;
          // DOM 更新了 操作dom时一定要在$nextTick里
          _this.$nextTick(() => {
            _this._initScroll();

            _this._calculateHeight();
          });
        }).catch((error) => {
          _this.$store.dispatch('LOADING_HIDE');
          console.log(error);
        })
      },

      //初始化 better-scroll
      _initScroll() {
        this.menuWrapper = new BScroll(this.$refs.menuWrapper, {
          click: true
        });
        this.foodsWrapper = new BScroll(this.$refs.foodsWrapper, {
          click: true,
          probeType: 3 // 监测实时滚动的位置
        });
        this.foodsWrapper.on('scroll', (position) => {
          this.scrollY = Math.abs(Math.floor(position.y));
        });
      },

      //计算右侧滚动块的高度
      _calculateHeight() {
        let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');

        let height = 0;
        this.listHeight.push(height);

        let len = foodList.length;

        for (let i = 0; i < len; i++) {
          let item = foodList[i];

          height += item.clientHeight;
          this.listHeight.push(height);
        }
      },

      //选择导航
      selectedMenu(index, event) {
        if (!event._constructed) {
          return;
        }
        //运用BScroll接口，滚动到相应位置
        let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
        //获取对应元素的列表
        let el = foodList[index];
        //设置滚动时间
        this.foodsWrapper.scrollToElement(el, 300);
      }
    },
    components: {
      SellHeader,
      control,
      goodFooter
    }
  }
</script>

<style scoped type="text/css" lang="less">
  @import "../../../styles/_mixins";

  .goods-detail {
    .nav {
      display: flex;
      width: 100%;
      height: 40px;
      line-height: 40px;
      .border-top-1px(rgba(7, 17, 27, 0.1));
      .border-bottom-1px(rgba(7, 17, 27, 0.1));
      .nav-items {
        flex: 1;
        text-align: center;
        font-size: 14px;
        color: rgb(77, 85, 93);
        &.nav-active {
          color: rgb(240, 20, 20);
        }
      }

    }
    .foods {
      position: absolute;
      top: 174px;
      bottom: 48px;
      display: flex;
      width: 100%;
      overflow: hidden;

      .menu-wrapper {
        flex: 0 0 80px;
        width: 80px;
        background: #f3f5f7;
        .menu-items {
          display: table;
          padding: 0 12px;
          width: 56px;
          height: 54px;
          line-height: 14px;
          &.current {
            position: relative;
            margin-top: -1px;
            z-index: 10;
            background: #fff;
            font-weight: 700;
            .text {
              .border-none();
            }
          }
          .text {
            display: table-cell;
            vertical-align: middle;
            width: 56px;
            font-size: 12px;
            .border-bottom-1px(rgba(7, 17, 27, 0.1));
            .icon {
              width: 12px;
              height: 12px;
              margin-right: 2px;
              background-size: 12px 12px;
              background-repeat: no-repeat;
              &.decrease {
                .background-image('../../../assets/images/decrease_3');
              }
              &.discount {
                .background-image('../../../assets/images/discount_3');
              }
              &.guarantee {
                .background-image('../../../assets/images/guarantee_3');
              }
              &.invoice {
                .background-image('../../../assets/images/invoice_3');
              }
              &.special {
                .background-image('../../../assets/images/special_3');
              }
            }
          }
        }
      }
      .foods-wrapper {
        flex: 1;
        background: @fff;
        width: 100%;
        .title {
          padding-left: 14px;
          height: 26px;
          line-height: 26px;
          border-left: 2px solid #d9dde1;
          font-size: 12px;
          color: rgb(147, 153, 159);
          background: #f3f5f7;
        }
        .food-item {
          display: flex;
          margin-left: 18px;
          padding: 18px 18px 18px 0;
          .border-bottom-1px(rgba(7, 17, 27, 0.1));
          &:last-child {
            margin-bottom: 0;
            .border-none();
          }
          .avatar {
            flex: 0 0 57px;
            margin-right: 10px;
          }
          .content {
            flex: 1;
            .food-name {
              margin-top: 2px;
              font-size: 14px;
              line-height: 14px;
              color: rgb(7, 17, 27);
            }
            .description {
              margin: 8px 0;
              font-size: 10px;
              line-height: 10px;
              color: rgb(147, 153, 159);
            }
            .extra {
              font-size: 10px;
              line-height: 10px;
              color: rgb(147, 153, 159);
              .sell-count {
                margin-right: 12px;
              }
            }
            .price {
              font-weight: 700;
              line-height: 24px;
              .now-price {
                margin-right: 8px;
                font-size: 14px;

                color: rgb(240, 20, 20);
              }
              .old-price {
                text-decoration: line-through;
                font-size: 10px;
                color: rgb(147, 153, 159);
              }
            }
            .control-wrapper {
              position: absolute;
              right: 10px;
              bottom: 12px;
            }
          }
        }
      }
    }
  }
</style>



