<template>
  <div class="goods-footer">
    <div class="content">
      <div class="cart-avatar">
        <span class="icon icon-shopping_cart"></span>
      </div>
      <div class="total-price">￥{{ totalPrice }}</div>
      <div class="desc">另需配送费￥{{ deliveryPrice }}元</div>
    </div>
    <div class="content-pay" :class="minPrice<totalPrice?'enough':'not-enough'">{{payDesc}}</div>
  </div>
</template>

<script>
  export default {
    name: 'goodsFooter',
    props: {
      minPrice: {
        type: Number,
        default: 0
      },
      deliveryPrice: {
        type: Number,
        default: 0
      },
      selectGoods: {
        type: Array,
        default() {
          return [];
        }
      }
    },
    data() {
      return {
        balls: [
          {
            show: false
          },
          {
            show: false
          },
          {
            show: false
          },
          {
            show: false
          },
          {
            show: false
          }
        ],
        dropBalls: [],
        fold: true /*折叠*/
      };
    },
    computed: {
      payDesc() {
        if (this.totalPrice === 0) {
          return `￥${this.minPrice}元起送`;
        } else if (this.totalPrice < this.minPrice) {
          let diff = this.minPrice - this.totalPrice;
          return `还差￥${diff}元起送`;
        } else {
          return '去结算'
        }
      },
      totalCount() {
        let count = 0;
        this.selectGoods.forEach((food) => {
          count += food.count;
        });
        return count;
      },
      totalPrice() {
        let total = 0;
        this.selectGoods.forEach((food) => {
          total += food.price * food.count;
        });
        return total;

      },
    },
    methods: {
      drop(el) {
        console.log(this.selectGoods);
        for (let i = 0; i < this.balls.length; i++) {
          let ball = this.balls[i];
          if (!ball.show) {
            ball.show = true;
            // 保存el
            ball.el = el;
            this.dropBalls.push(ball);
            return;
          }
        }
      },
    }
  }
</script>

<style scoped type="text/css" lang="less">
  @import "../../styles/_mixins";

  .goods-footer {
    position: fixed;
    display: flex;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 48px;
    background: #141d27;
    .content {
      flex: 1;
      font-size: 0;
      .cart-avatar {
        position: relative;
        left: 0;
        bottom: 10px;
        display: inline-block;
        vertical-align: top;
        margin: 0 18px;
        width: 44px;
        height: 44px;
        border: 6px solid #141d27;
        border-radius: 50%;
        background: #2b343c;
        text-align: center;
        .icon {
          font-size: 24px;
          line-height: 44px;
          color: #80858a;
        }
      }
      .total-price {
        display: inline-block;
        vertical-align: top;
        margin: 12px 0;
        padding-right: 12px;
        font-size: 18px;
        color: rgba(255, 255, 255, .4);
        font-weight: 700;
        line-height: 24px;
        .border-right-1px(rgba(255, 255, 255, .1));
      }
      .desc {
        display: inline-block;
        vertical-align: top;
        margin: 0 12px;
        height: 100%;
        line-height: 48px;
        font-size: 10px;
        color: rgba(255, 255, 255, .4);
      }
    }
    .content-pay {
      padding-left: 8px;
      width: 97px;
      text-align: center;
      height: 48px;
      line-height: 48px;
      font-size: 12px;
      font-weight: 700;
      background: rgba(0, 0, 0, 0.2);
      color: rgba(255, 255, 255, 0.4);
      &.not-enough {
        background: #2b333b;
      }
      &.enough {
        background: #00b43c;
        color: #fff;
      }
    }
  }
</style>



