import Profile from '../../../pages/profile/Profile.vue'

import defaultPath from './childern/default'
import collection from './childern/collection'
import indexHome from "./childern/indexHome"
import receive from "./childern/receive"
import editReceive from "./childern/editReceive";
import account from "./childern/account";
import bindMobile from "./childern/bindMobile";
import editPayPwd from "./childern/editPayPwd";
import cooperation from "./childern/cooperation";
import setting from "./childern/setting";
import settingCommon from "./childern/settingCommon";
import about from "./childern/about";
import mobileSign from "./childern/mobileSign";
import passwordSign from "./childern/passwordSign";


export default {
  path: '/profile',
  name: 'Profile',
  component: Profile,
  children: [
    defaultPath,
    indexHome,
    collection,
    receive,
    editReceive,
    account,
    bindMobile,
    editPayPwd,
    cooperation,
    setting,
    settingCommon,
    about,
    mobileSign,
    passwordSign
  ]
}




