import Goods from '../../../pages/goods/Goods.vue'

import defaultPath from './children/default'
import indexHome from "./children/indexHome"
import goodsDetail from './children/goodsDetail'

export default {
  path: '/goods',
  name: 'Goods',
  component: Goods,
  children: [
    defaultPath,
    indexHome,
    goodsDetail
  ]
}




