<template>
  <div class="swiper-container banner-swiper">
    <div class="swiper-wrapper banner-wrapper">
      <div class="swiper-slide">
        <img class="banner" src="../../assets/images/usr-name.jpeg" alt="" width="100%" height="100%">
      </div>
      <div class="swiper-slide">
        <img class="banner" src="../../assets/images/usr-name.jpeg" alt="" width="100%" height="100%">
      </div>
    </div>
    <!-- 如果需要分页器 -->
    <div class="swiper-pagination banner-pagination"></div>
  </div>
</template>

<script>
  export default {
    name: 'BannerSwiper',
    mounted() {
      new Swiper('.banner-swiper', {
        spaceBetween: 30,
        slidesPerView: 1,
        loop: true,
        pagination: {
          el: '.banner-pagination'
        }
      });
    }
  }
</script>

<style scoped type="text/css" lang="less">
  .banner-swiper {
    width: 100%;
    .banner-wrapper {
      .swiper-slide {
        width: 100%;
        height: 100px;
      }
    }
  }
</style>



