<template>
  <div class="goods-control">
    <span class="move" v-show="food.count && food.count>0" @click.stop.prevent="construct">-</span>
    <span class="count" v-show="food.count && food.count>0">{{food.count}}</span>
    <span class="add" @click.stop.prevent="add">+</span>
  </div>
</template>

<script>
  import Vue from 'vue'
  //  import Bus from '../../services/event/eventBus'

  export default {
    name: 'control',
    props: {
      food: {
        type: Object
      }
    },
    data() {
      return {}
    },
    methods: {
      add(event) {
        //首次点击food对象没有count属性
        if (!this.food.count) {
          //Vue.set是添加属性,vue会脏检查
          Vue.set(this.food, 'count', 1);
        } else {
          this.food.count++;
        }
        console.log(this.food);
//        Bus.$emit('add.count', event.target);

      },
      construct(event) {
        if (this.food.count) {
          this.food.count--;
        }

      }
    }
  }
</script>

<style scoped type="text/css" lang="less">
  @import "../../styles/_mixins";

  .goods-control {
    font-size: 0;
    text-align: center;
    .move {
      display: inline-block;
      width: 20px;
      height: 20px;
      font-size: 24px;
      line-height: 20px;
      border-radius: 12px;
      vertical-align: middle;
      border: 2px solid @blue;
      color: @blue;
    }
    .count {
      display: inline-block;
      width: 24px;
      height: 24px;
      line-height: 24px;
      vertical-align: middle;
      text-align: center;
      font-size: 10px;
      color: rgb(147, 153, 159);
    }
    .add {
      display: inline-block;
      width: 24px;
      height: 24px;
      font-size: 24px;
      line-height: 24px;
      border-radius: 12px;
      vertical-align: middle;
      background: @blue;
      color: @fff;
    }
  }
</style>



