<template>
  <div class="goods-wrapper">
    <div class="content-box" @click="changeAddress('/goods/detail')">
      <img src="../../assets/images/usr-name.jpeg" alt="" width="64" height="64">
      <div class="content">
        <div class="basic-content">
          <div class="row-content">
            <img src="../../assets/images/usr-name.jpeg" alt="" height="16" width="24">
            <div class="store-title"><span>果之满满鲜榨果汁吧</span>&nbsp;(<span>陆软店</span>)</div>
            <div class="other-msg">
              <span class="ensure ensure-b">保</span><span class="ensure ensure-p">票</span>
            </div>
          </div>
          <div class="row-content">
            <div class="star-wrapper">
              <Star :imageSize="10" :score="score"></Star>
              <span class="store-score">{{score}}</span>
              <span class="sell-count">月售{{sellCount}}单</span>
            </div>
            <div class="other-msg">
              <span class="distribution">准时送</span><span class="distribution distribution-bg">蜂鸟专送</span>
            </div>
          </div>
          <div class="row-content">
            <div class="min-price">¥20起送 | 配送费¥5元</div>
            <div class="other-msg">
              <span>1.5km</span><span>|</span><span>31分钟</span>
            </div>
          </div>
        </div>
        <div class="price-content">
          <div class="row-content">
            <div class="message">
              <span class="icon" :class="classMap[0]"></span><span class="text">满80减15,满110减35</span>
            </div>
            <div class="message">
              <span class="icon" :class="classMap[0]"></span><span class="text">满80减15,满110减35</span>
            </div>
          </div>
          <div class="total">9个活动</div>
        </div>
        <div class="addr-content">
          <div class="addr">
            <span class="icon" :class="classMap[0]"></span><span class="text">满80减15,满110减35</span>
          </div>
          <span class="icon icon-keyboard_arrow_right"></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Star from '../../components/star/Star'

  export default {
    name: 'GoodsList',
    data() {
      return {
        score: 4.6,
        sellCount: 577,
        classMap: ['decrease', 'discount', 'guarantee', 'invoice', 'special']    //初始化优惠的活动
      }
    },
    methods: {
      //页面跳转
      changeAddress(path) {
        this.$router.push(path);
      }
    },
    components: {
      Star
    }
  }
</script>

<style scoped type="text/css" lang="less">
  @import "../../styles/_mixins";

  .goods-wrapper {
    width: 100%;
    .content-box {
      display: flex;
      margin-top: 10px;
      .border-bottom-1px(@c9);
      .content {
        margin-left: 8px;
        flex: 1;
        .basic-content {
          .border-bottom-1px(@c9);
          .row-content {
            display: flex;
            padding-bottom: 4px;
            font-size: 12px;
            .store-title {
              flex: 1;
              margin-left: 4px;
              font-size: 14px;
              font-weight: 600;
            }
            .star-wrapper {
              flex: 1;
              .star {
                display: inline-block;
              }
              .store-score, .sell-count {
                display: inline-block;
                height: 16px;
                line-height: 16px;
                margin-left: 4px;
              }
            }
            .min-price {
              flex: 1;
            }
            .other-msg {
              span {
                display: inline-block;
                padding: 2px;
                margin-left: 8px;
                border-radius: 2px;
                &.ensure {
                  border: 1px solid @bc;
                  color: @ccc;
                }
                &.distribution {
                  border: 1px solid @blue;
                  color: @blue;
                }
                &.distribution-bg {
                  background-color: @blue;
                  color: @fff;
                }
              }
            }
          }
        }
        .price-content {
          display: flex;
          margin-top: 6px;
          .border-bottom-1px(@c9);
          .row-content {
            flex: 1;
            .message {
              font-size: 0;
              margin-bottom: 8px;
              .icon {
                display: inline-block;
                vertical-align: top;
                width: 14px;
                height: 14px;
                background-size: 14px 14px;
                background-repeat: no-repeat;
                &.decrease {
                  .background-image('../../assets/images/decrease_1');
                }
                &.discount {
                  .background-image('../../assets/images/discount_1');
                }
                &.guarantee {
                  .background-image('../../assets/images/guarantee_1');
                }
                &.invoice {
                  .background-image('../../assets/images/invoice_1');
                }
                &.special {
                  .background-image('../../assets/images/special_1');
                }
              }
              .text {
                display: inline-block;
                margin-left: 6px;
                font-size: 12px;
                line-height: 12px;
                font-weight: 200;
              }
            }
          }
          .total {
            font-size: 12px;
          }
        }
        .addr-content {
          display: flex;
          margin: 8px 0 12px 0;
          .addr {
            flex: 1;
            font-size: 0;
            .icon {
              display: inline-block;
              vertical-align: top;
              width: 14px;
              height: 14px;
              background-size: 100% 100%;
              background: url("../../assets/images/usr-name.jpeg") no-repeat;
            }
            .text {
              margin-left: 6px;
              font-size: 14px;
            }
          }
          .icon-keyboard_arrow_right {
            display: inline-block;
            transform: rotate(90deg);
          }
        }
      }
    }
  }
</style>



