<template>
  <div class="home-indexHome">
    <div class="container find-container">
      <div class="cur-env">
        <div class="position">
          <span class="icon icon-add_circle"></span><span class="title">陆家嘴软件园</span><span class="icon icon-check_circle"></span>
        </div>
        <div class="weather">
          <span class="title-box">
            <p>17°</p>
            <p>小雨</p>
          </span>
          <img src="../../../assets/images/usr-name.jpeg" alt="" width="24" height="24">
        </div>
      </div>
      <div class="search-box">
        <input class="search" type="text" placeholder="输入商家、商品名称">
        <div class="hint">
          <span v-for="txt in hintTitle">{{txt}}</span>
        </div>
      </div>
    </div>
    <BtnGroupSwiper :toolsBtnArr="toolsBtnArr"></BtnGroupSwiper>
    <BannerSwiper></BannerSwiper>
    <div class="container buying-container">
      <div class="wrapper">
        <div class="content-box margin-right position-relative">
          <p class="title">限量抢购</p>
          <p class="price">超值美味 9.9元起</p>
          <p class="entry">5106 人正在抢 <span class="icon icon-keyboard_arrow_right"></span></p>
          <img src="../../../assets/images/usr-name.jpeg" alt="" width="64" height="64" class="img-right">
        </div>
        <div class="content-box margin-left">
          <p class="title">半价大牌菜</p>
          <p class="price">立减20元起</p>
          <p class="entry">立即嗨吃 <span class="icon icon-keyboard_arrow_right"></span></p>
          <img src="../../../assets/images/usr-name.jpeg" alt="" width="64" height="64" class="img-right">
        </div>
      </div>
      <div class="wrapper">
        <div class="content-box txt-center margin-right">
          <p class="title">营养快餐</p>
          <p class="des">海量美食任选</p>
          <img src="../../../assets/images/usr-name.jpeg" alt="" width="64" height="64">
        </div>
        <div class="content-box txt-center margin-left margin-right">
          <p class="title">简餐大咖</p>
          <p class="des">比吃榜</p>
          <img src="../../../assets/images/usr-name.jpeg" alt="" width="64" height="64">
        </div>
        <div class="content-box txt-center margin-left">
          <p class="title">吃货狂欢节</p>
          <p class="des">满40减20起</p>
          <img src="../../../assets/images/usr-name.jpeg" alt="" width="64" height="64">
        </div>
      </div>
    </div>
    <div class="preferred">
      <FoodsHeader></FoodsHeader>
      <div class="wrapper">
        <div class="content-box txt-center margin-right">
          <img src="../../../assets/images/usr-name.jpeg" alt="" width="64" height="64">
          <p class="title">鹅佬柒铺</p>
          <p class="des">大牌精选</p>
        </div>
        <div class="content-box txt-center margin-left margin-right">
          <img src="../../../assets/images/usr-name.jpeg" alt="" width="64" height="64">
          <p class="title">必胜客宅急送</p>
          <p class="des">大牌精选</p>
        </div>
        <div class="content-box txt-center margin-left margin-right">
          <img src="../../../assets/images/usr-name.jpeg" alt="" width="64" height="64">
          <p class="title">上海 小南国</p>
          <p class="des">大牌精选</p>
        </div>
        <div class="content-box txt-center margin-left">
          <img src="../../../assets/images/usr-name.jpeg" alt="" width="64" height="64">
          <p class="title">拿渡麻辣香锅</p>
          <p class="des">大牌精选</p>
        </div>
      </div>
    </div>

    <div class="recommend">
      <header class="header">推荐商家</header>
      <StoreList></StoreList>
      <footer class="footer">没有更多了</footer>
    </div>
  </div>
</template>

<script>
  import StoreList from '../../../components/goods/GoodsList'
  import FoodsHeader from '../../../components/header/FoodsHeader'
  import BtnGroupSwiper from '../../../components/swiper/BtnGroupSwiper'
  import BannerSwiper from '../../../components/swiper/BannerSwiper'

  export default {
    name: 'HomeIndexHome',
    data() {
      return {
        hintTitle: ['炸鸡', '沙拉', '奶茶', '汉堡王', '麦当劳', '一点点', '粥', '蛋糕', '鸡排'],
        toolsBtnArr: [
          [
            {avatar: '/static/images/usr-name.jpeg', title: '美食'},
            {avatar: '/static/images/usr-name.jpeg', title: '晚餐'},
            {avatar: '/static/images/usr-name.jpeg', title: '商超便利'},
            {avatar: '/static/images/usr-name.jpeg', title: '预定早餐'},
            {avatar: '/static/images/usr-name.jpeg', title: '果蔬生鲜'},
            {avatar: '/static/images/usr-name.jpeg', title: '新店特惠'},
            {avatar: '/static/images/usr-name.jpeg', title: '大牌5折'},
            {avatar: '/static/images/usr-name.jpeg', title: '帮买帮送'}
          ],
          [
            {avatar: '/static/images/usr-name.jpeg', title: '甜品饮品'},
            {avatar: '/static/images/usr-name.jpeg', title: '医疗健康'},
            {avatar: '/static/images/usr-name.jpeg', title: '浪漫鲜花'},
            {avatar: '/static/images/usr-name.jpeg', title: '麻辣烫'},
            {avatar: '/static/images/usr-name.jpeg', title: '汉堡'},
            {avatar: '/static/images/usr-name.jpeg', title: '地方菜系'},
            {avatar: '/static/images/usr-name.jpeg', title: '披萨意面'},
            {avatar: '/static/images/usr-name.jpeg', title: '准时达'}
          ]
        ],
        seller: {}

      }
    },
    mounted() {
      //初始化底部导航栏
      this.$store.dispatch("FOOTER_SHOW");
    },
    methods: {
      //页面跳转
      changeAddress(path) {
        this.$router.push(path);
      }
    },
    components: {
      BtnGroupSwiper,
      BannerSwiper,
      FoodsHeader,
      StoreList,

    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  @import "../../../styles/_mixins";

  .home-indexHome {
    width: 100%;
    padding-bottom: 60px;
    .container {
      padding: 20px 10px 8px 10px;
    }
    .find-container {
      padding: 20px 10px 8px 10px;
      background: @blue;
      color: @fff;
      .cur-env {
        display: flex;
        align-items: center;
        padding-bottom: 10px;
        .position {
          flex: 1;
          .title {
            font-size: 20px;
          }
          .icon-add_circle {
            margin-right: 8px;
          }
          .icon-check_circle {
            margin-left: 8px;
          }
        }
        .weather {
          .title-box {
            display: inline-block;
            font-size: 12px;
          }
        }
      }
      .search-box {
        .search {
          padding: 0 8px;
          width: 100%;
          height: 30px;
          box-sizing: border-box;
          font-size: 14px;
          &::-webkit-input-placeholder, &:-moz-placeholder, &::-moz-placeholder, &:-ms-input-placeholder {
            text-align: center;
          }
        }
        .hint {
          display: flex;
          align-items: center;
          margin-top: 10px;
          font-size: 12px;
          span {
            flex: 1;
            text-align: center;
          }
        }
      }
    }
    .buying-container {
      background-color: @fff;
      .wrapper {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-bottom: 5px;
        align-content: space-between;

        .content-box {
          background-color: #F5F5F5;
          flex: 1;
          padding: 10px 0 4px 8px;
          &.position-relative {
            position: relative;
          }
          &.txt-center {
            text-align: center;
          }
          .title {
            margin-bottom: 6px;
            font-size: 18px;
          }
          .price, .entry {
            margin-bottom: 6px;
            font-size: 14px;
            vertical-align: middle;
            .icon-keyboard_arrow_right {
              vertical-align: middle;
            }
          }
          .des {
            margin-bottom: 6px;
            display: inline-block;
            width: auto;
            font-size: 14px;
            border: 1px solid #e4e4e4;
          }
          .txt-center {
            text-align: center;
          }
          .img-right {
            position: relative;
            top: 0;
            left: 100%;
            transform: translateX(-120%);
          }
          .img-center {
            position: relative;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
          }
        }
        .margin-left {
          margin-left: 2px;
        }
        .margin-right {
          margin-right: 2px;
        }
      }
    }
    .preferred {
      margin-top: 10px;
      padding: 0 10px;
      background-color: @fff;
      .wrapper {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-bottom: 5px;
        padding-bottom: 14px;
        text-align: center;
        align-content: space-between;
        .content-box {
          flex: 1;
          .title {
            margin-top: 6px;
            font-size: 14px;
          }
          .des {
            margin-top: 4px;
            display: inline-block;
            font-size: 12px;
            border: 1px solid #3190e8;
            color: #3190e8;
          }
        }
        .margin-left {
          margin-left: 2px;
        }
        .margin-right {
          margin-right: 2px;
        }
      }
    }
    .recommend {
      margin-top: 10px;
      padding: 0 10px;
      background-color: @fff;
      .header {
        width: 100%;
        height: 40px;
        line-height: 40px;
      }
    }
    .footer {
      width: 100%;
      height: 40px;
      line-height: 40px;
      text-align: center;
    }
  }


</style>



