<template>
  <div class="goods-indexHome">
    <GoodsHeader :isGoBack="isGoBack" :title="title"></GoodsHeader>
    <div class="tabs">
      <div class="swiper-container tabs-group-container">
        <div class="swiper-wrapper tabs-group-wrapper">
          <div class="swiper-slide tabs-slide" v-for="val in tabsArr">{{val}}</div>
        </div>
      </div>
      <span class="icon icon-keyboard_arrow_right"></span>
    </div>
    <div class="select">
      <div class="sort">综合排序<span class="icon icon-keyboard_arrow_right"></span></div>
      <div class="preferred">好评优先</div>
      <div>距离最近</div>
      <div>筛选<span class="icon icon-keyboard_arrow_right"></span></div>
    </div>
    <div class="container">
      <GoodsList></GoodsList>
    </div>
  </div>
</template>

<script>
  import GoodsHeader from '../../../components/header/GoodsHeader'
  import GoodsList from '../../../components/goods/GoodsList'

  import Star from '../../../components/star/Star'

  import Swiper from '../../../../static/js/swiper.min'


  export default {
    name: 'GoodsIndexHome',
    data() {
      return {
        title: '',
        isGoBack: true,
        seller: {},
        tabsArr: ['全部', '香锅冒菜', '简餐便当', '面食粥点', '汉堡披萨', '日韩料理', '轻食西餐', '小吃炸串', '地方菜系']
      }
    },
    mounted() {
      //初始化底部导航栏
      this.$store.dispatch("FOOTER_HIDE");
      //获取路由参数
      this.title = this.$route.params.title;
      //初始化导航栏
      new Swiper('.tabs-group-container', {
        slidesPerView: 5,
        spaceBetween: 10,
        freeMode: true,
      });
    },
    methods: {},
    components: {
      GoodsHeader,
      GoodsList,
      Star
    }
  }
</script>

<style scoped type="text/css" lang="less">
  @import "../../../styles/_mixins";

  .goods-indexHome {
    padding-top: 44px;
    width: 100%;
    min-height: 100%;
    padding-bottom: 60px;
    .tabs {
      display: flex;
      padding-right: 10px;
      height: 44px;
      align-items: center;
      background: @blue;
      font-size: 14px;
      color: @fff;
      .tabs-group-container {
        flex: 1;
        padding-right: 5px;
        .tabs-group-wrapper {
          .tabs-slide {
            text-align: center;
          }
        }

      }
      .icon-keyboard_arrow_right {
        display: inline-block;
        transform: rotate(90deg);
      }
    }
    .select {
      display: flex;
      align-items: center;
      margin-top: 10px;
      padding: 0 10px;
      height: 40px;
      .border-bottom-1px(@bc);
      background: @fff;
      & > div {
        flex: 1;
        text-align: center;
        .icon-keyboard_arrow_right {
          display: inline-block;
          transform: rotate(90deg);
        }
      }
    }
    .container {
      padding: 1px 10px 8px 10px;
      background: @fff;
    }
  }
</style>



