<template>
  <div class="swiper-container btn-group-swiper">
    <div class="swiper-wrapper btn-group-wrapper">
      <div class="swiper-slide" v-for="arr in toolsBtnArr">
        <div class="btn-group">
          <div class="btn-tools" v-for="obj in arr" @click="changeAddress('/goods/indexHome/'+obj.title)">
            <img :src="obj.avatar" alt="" width="36" height="36">
            <p class="title">{{obj.title}}</p>
          </div>
        </div>
      </div>
    </div>
    <!-- 如果需要分页器 -->
    <div class="swiper-pagination btn-group-pagination"></div>
  </div>
</template>

<script>
  import Swiper from '../../../static/js/swiper.min'

  export default {
    name: 'BtnGroupSwiper',
    props: ['toolsBtnArr'],
    mounted() {
      //初始化工具条轮播图
      new Swiper('.btn-group-swiper', {
        pagination: {
          el: '.btn-group-pagination',
        }
      });
    },
    methods: {
      //页面跳转
      changeAddress(path) {
        if (_czc) {
          console.log(123);
          let aPath = path.split('/');
          let category = "首页";
          _czc.push(["_trackEvent", category, aPath[aPath.length - 1]]);
        }
        this.$router.push(path);
      }
    }
  }
</script>

<style scoped type="text/css" lang="less">
  @import "../../styles/_mixins";

  .btn-group-swiper {
    width: 100%;
    background: @fff;
    .btn-group-wrapper {
      .swiper-slide {
        .btn-group {
          display: flex;
          flex-wrap: wrap;
          align-items: center;
          padding-bottom: 20px;
          .btn-tools {
            padding-top: 10px;
            text-align: center;
            width: 25%;
            .title {
              margin-top: 4px;
            }
          }
        }
      }
    }
    .btn-group-pagination {
      bottom: 5px;
    }

  }
</style>



